<template lang="pug">
    div.content-setting-index
        div.setting-index
            list-block  
                list-item(link="/user/setting/safety" itemTitle="账户与安全")
            list-block  
                list-item(link="/user/setting/blacklist" itemTitle="黑名单")
            list-block
                list-item(link="/user/setting/help" itemTitle="帮助与反馈")
                list-item(link="/user/setting/article_cate" itemTitle="关于我们")
            div.login-out(@click="loginOut") 退出登录

</template>
<script>
import listBlock from '@/components/listBlock'
import listItem from '@/components/listItem'
import { Toast } from 'mint-ui'
export default {
    name: 'settingIndex',
    components: {
        listBlock,
        listItem
    },
    methods: {
        loginOut() {
            this.axios.post(this.api.loginLoginout()).then(res => {
                if(res.data.status == 1) {
                    let instance = Toast(res.data.error || '操作成功');
                    setTimeout(() => {
                        instance.close();
                        this.$router.push({
                            path: '/login/index'
                        });
                    },1000);
                }
                else {
                    Toast(res.data.error || '操作失败');
                }
            })
            .catch(err => {
                Toast(err || '请求失败');
            })
        }
    }
}
</script>
<style lang="less" scoped>
    @import '../../../assets/css/variable.less';
    .login-out{
        height: 2rem;
        line-height: 2rem;
        text-align: center;
        background-color: #fff;
        margin-top: .5rem;
        color: @color-red;
    }
</style>



